
  #buscador{
    background-color: white;
  }
  #map{
    width: 100%;
    height: 90%;
    border: 2px solid black;
  }
  
  button{
    margin: 15px auto;
    padding: 10px 15px;
    color: white;
    background-color: #f756b4;
    border: 0;
    font-weight: 700;
    border-radius: 2px;
    font-family: 'Raleway', sans-serif;
    font-family: 'Passion One', cursive ;
    font-size: 15px;
    display:block;
  }
  
.formT input{
    margin: 14px auto;
    padding: 3px 5px;
    font-family: 'Raleway', sans-serif;
    font-family: 'Passion One', cursive ;
    font-size: 15px;
    display:block;
    width: 300px;
  }
  
  button:hover, button:focus{
    outline: 0;
  }
  
  #carLyft{
    width: 80px;
    height: 80px;
  }
  
  #usuario{
    position: absolute;
    top: 15px;
    font-size: 40px;
    z-index: 5;
  }


